import React, {useEffect, useState} from 'react';
import styled from 'styled-components';
// import axios from 'axios';


const CommunityBlock = styled.div`
    border-top:107px solid orange;
    padding:100px 0;
    .row {
        h2 { text-align:center; font-size:30px }
        ul {
            display:flex; 
            flex-wrap:wrap;
            li { width:25%; margin:20px 0 }
        }
    }
`;

const Communitysection = () => {

    const [cleanair, setCleanair] = useState()

    // const getAir = async () => {
    //     const kim = await axios.get('https://mykim.herokuapp.com/http://apis.data.go.kr/B552584/UlfptcaAlarmInqireSvc/getUlfptcaAlarmInfo?serviceKey=tx7kx27i54c7VtH93Tgfl0x9L3ws5WSFsEH%2FjynITwxxDnph%2B%2BaBIIJmAlBpAKpZ2aNvgoDFrZ7enPHAtd2rwA%3D%3D&returnType=json&numOfRows=100&pageNo=1&year=2020&itemCode=PM10')
    //     console.log(kim)
    //     setCleanair(kim.data.response.body.items)
    // }

    // const getAir = (myCallback) => {
    //     var xhr = new XMLHttpRequest()
    //     xhr.open('GET', 'https://mykim.herokuapp.com/http://apis.data.go.kr/B552584/UlfptcaAlarmInqireSvc/getUlfptcaAlarmInfo?serviceKey=tx7kx27i54c7VtH93Tgfl0x9L3ws5WSFsEH%2FjynITwxxDnph%2B%2BaBIIJmAlBpAKpZ2aNvgoDFrZ7enPHAtd2rwA%3D%3D&returnType=json&numOfRows=100&pageNo=1&year=2020&itemCode=PM10', true)
    //     xhr.onload = function(){
    //         if (xhr.status===200) {
    //             var kim = JSON.parse(xhr.responseText) 
    //             console.log(kim)
    //             myCallback(kim.response.body.items)
    //         } else {
    //             myCallback("")
    //         }
    //     }
    //     xhr.send();
    // }

    const getAir = new Promise(
        (myCallback, reject) => {
        let xhr = new XMLHttpRequest()
        xhr.open('GET', 'https://mykim.herokuapp.com/http://apis.data.go.kr/B552584/UlfptcaAlarmInqireSvc/getUlfptcaAlarmInfo?serviceKey=tx7kx27i54c7VtH93Tgfl0x9L3ws5WSFsEH%2FjynITwxxDnph%2B%2BaBIIJmAlBpAKpZ2aNvgoDFrZ7enPHAtd2rwA%3D%3D&returnType=json&numOfRows=100&pageNo=1&year=2020&itemCode=PM10', true)
        xhr.onload = function(){
            if (xhr.status===200) {
                // console.log(xhr.responseText)
                let kim = JSON.parse(xhr.responseText) 
                let items = kim.response.body.items
                myCallback(items)
            } else {
                reject("")
            }
        }
        xhr.send();
    })


    useEffect( ()=>{
        // getAir()
        // getAir(setCleanair)
        getAir.then( (value) => { setCleanair(value) } ).catch( (error) => setCleanair(error) )
    }, [cleanair] )

    return (
        <CommunityBlock>
            <div className="row">
                <h2>에어코리아 미세먼지 경고발령 현황</h2>
                <ul>
                    { cleanair ? cleanair.map( (data, index) => <li key={index}>{data.districtName} : {data.issueVal}</li> ) : <li style={{ fontSize:'50px', textAlign:'center', padding:'50px 0', width:'100%'}}>File Loading~~</li> }
                </ul>
            </div>
        </CommunityBlock>
    );
};

export default Communitysection;